    /** 轮次发牌数量配置 index为轮次, value为玩家发牌数数组(庄家本地座位开始逆时针)*/
    public static dealConfig = [
        // index与getDealOrderArr计算出来的发牌数组的index一一对应
        // 配置解析: 前三轮每家各发4张, 第四轮庄发2张其他发1张
        [4, 4, ],     
        [4, 4, ],
        [4, 4, ],
        [2, 1, ],
    ]

    /**
     * 计算拿牌起点
     * @return [起点牌墙本地座位, 当前牌墙里的起点索引]
     * @param bankerLocalSeat 庄家本地座位
     * @param dice 
     */
    public static calculateDealBeigin(bankerLocalSeat: number, dice: number[]){
        let total = 0;
        dice.forEach(num=>{
            total += num;
        })
        // 当前确定方案为: 点数和为奇数表示摸牌起点为庄的对家, 偶数为庄自家, 和值为从当前牌墙第几墩开始拿 *2即从第whoStart家的第几张开始拿(索引从0开始)
        let whoStart = total % 2;
        let offset = whoStart - Game.Context.selfLocalSeat;
        
        return [(bankerLocalSeat + offset) % 2, 2 * total];
    }

    /**
     * 获取庄家开始的摸牌顺序本地座位数组 逆时针
     * @param bankerLocalSeat 庄家本地座位
     */
    public static getDealOrderArr(bankerLocalSeat: number){
        let arr = [];
        for(let i = 0; i < ErmjGameConst.maxPlayerCount; i++){
            let r = (bankerLocalSeat + i + ErmjGameConst.maxPlayerCount) % ErmjGameConst.maxPlayerCount;     // 逆时针摸牌
            arr.push(r);
        }
        return arr;
    }

    /** 麻将值数组排序 */
    public static sortMjValueArr(arr: number[]){
        if (!arr || Global.Toolkit.isEmptyObject(arr))
            return [];
        let tmp = [...arr];
        tmp.sort((a, b)=> a - b);
        return tmp;
    }

    public static getMjValueType(value: number){
        if (value >= 1 && value <= 9)
            return MjValueType.Wan;
        if (value >= 11 && value <= 19)
            return MjValueType.Tiao;
        if (value >= 21 && value <= 29)
            return MjValueType.Tong;
        if (value >= 31 && value <= 37)
            return MjValueType.Zi;
        if (value >= 41 && value <= 48)
            return MjValueType.Hua;
    }

    public static checkAlmostWin(value: number, tingArr: any[]){
        if (!tingArr || Global.Toolkit.isEmptyObject(tingArr))
            return false;
        let valueType = this.getMjValueType(value);
        if (!valueType || valueType == MjValueType.Hua)     // 花牌不搓
            return false;
        for(let i = 0; i < tingArr.length; i++){
            let type = this.getMjValueType(tingArr[i].card);
            if(type == valueType)
                return true;
        }
        return false;
    }
